









































































































































































import { Component, Prop, Vue, Emit } from "vue-property-decorator";

@Component
export default class Header extends Vue {
  @Prop() text!: string;
  expand = false;
  dateFrom = null;
  dateTo = null;
  menuFrom = false;
  menuTo = false;
  filterApplied = false;

  tags = [2];

  applyFilter() {
    this.filterApplied = true;
    this.expand = false;
  }

  cancelFilter() {
    this.tags = [];
    this.dateFrom = null;
    this.dateTo = null;
    this.filterApplied = false;
  }

  goToTwitter() {
    window.location.href = "https://twitter.com/coloridospillos";
  }

  goToHome() {
    this.expand = false;
    this.$router.push("/");
  }

  goToPilleada() {
    this.expand = false;
    this.$router.push("/pilleada");
  }

  cancelMenu1() {
    this.menuFrom = false;
    this.dateFrom = null;
  }

  cancelMenu2() {
    this.menuTo = false;
    this.dateTo = null;
  }
}
